<template>
    <div class="col-3 d-flex flex-column">        
        <h5 class="mt-3">Channels</h5>        
        <p>You should not update Channel often.</p>

        <!-- <button class="btn btn-block btn-primary" name="tippy"><i class="las la-plus-circle"></i> Add Channel</button> -->
        <v-popover 
            :open="showChannelAddPopover"
            @show="focusOnInput">
            <button @click="showChannelAddPopover = true" class="btn btn-block btn-primary" name="tippy"><i class="las la-plus-circle"></i> Add Channel</button>
            <template slot="popover">
                <form @submit.prevent="channelSubmit" class="d-flex p-2 align-items-center">
                    <input ref="input" :disabled="loading" v-model="channelInput" class="form-control mr-2" placeholder="Channel Name"/>
                    <button v-if="!loading" class="btn btn-primary">Submit</button>
                    <spinner v-else />
                </form>
            </template>
        </v-popover>


        <!-- <tippy-component 
            to="tippy" 
            trigger="click"
            interactive="true"
            theme="light custom"
            arrow
            @shown="focusOnInput">
            <form @submit.prevent="submit" class="p-2">
                <input 
                    style="width: 300px"
                    ref="input" 
                    v-model="channelInput" 
                    class="form-control" 
                    placeholder="Channel Name"/>
                <div class="d-flex flex-row-reverse mt-2">
                    <button class="btn btn-primary">Submit</button>
                </div>
            </form>
        </tippy-component> -->
        <!-- <div class="d-flex justify-content-between channel" 
            :key="l.ID" 
            v-for="l in list">
            <span>{{l.Name}}</span>
            <v-popover>
                <i class="las la-ellipsis-v"></i>
                
            </v-popover>
        </div> -->
         <loading-overlay :loading="loadingOverlay" class="flex-fill overflow-h">
        <div class="d-flex justify-content-between channel" 
            :key="l.ID" 
            v-for="l in list"
            @click="showModal(l)"
            >            
            <span>{{l.Name}}</span>
            <i name="more" class="las la-ellipsis-v"></i>
        </div>
         </loading-overlay>

        <modal name="channel" height="auto" width="400">            
            <div class="p-2">
                <spinner v-if="modalLoading"/>
                <template v-else>
                <h4>{{editInput}}<a @click="confirmRemove" href="#" class="float-right text-danger mr-2"><i class="lar la-trash-alt"></i></a></h4>
                <input ref="editInput" class="form-control" v-model="editInput"/>
                <button class="btn btn-primary float-right my-2 btn-sm"
                @click="update">Update</button>                    
                <div class="clearfix"></div>
                </template>                
            </div>            
        </modal>
        <v-dialog/>
    </div>
</template>
<script>
// import { TippyComponent } from "vue-tippy";
import axios from 'axios';
import Spinner from "vue-simple-spinner";
import auth from '@/services/auth';
import LoadingOverlay from "@/components/Loaders/LoadingOverlay";
export default {
    components:{
        Spinner,
        LoadingOverlay    
        // TippyComponent
    },
    data() {
        return {
            list: [],
            channelInput: '',
            updateTippies: false,
            msg: 'i cant',
            showChannelAddPopover: false,
            loading: false,
            modalLoading: false,
            activeChannel: {},
            editInput:'',
            loadingOverlay:false
        }
    },
    methods:{
        focusOnInput() { 
            this.$refs.input.focus() 
        },
        showModal(l){
            this.modalLoading = false;
            this.activeChannel = l;
            this.editInput = l.Name;
            this.$modal.show('channel')
            setTimeout(() => {
                this.$refs.editInput.focus();
            }, 100);
        },
        channelNameAlreadyExist(){      
            this.$modal.show('dialog',{
                text: `Channel name "${this.channelInput}" already exist!`,
                buttons:[
                    {title: 'Close'}
                ],              
            })
        },
        channelNameAlreadyExistForEdit(){      
            this.$modal.show('dialog',{
                text: `Channel name "${this.editInput}" already exist!`,
                buttons:[
                    {title: 'Close'}
                ],              
            })
        },
        async channelSubmit(){
            var foundIndex = this.list.findIndex(x => x.Name == this.channelInput);
            if(this.channelInput && foundIndex<0){
                this.loading = true;
                try{
                    var res = await axios.post("channel/upsert",{
                        Title: this.channelInput
                    });
                    if(res.status == 200){
                        this.list.push({
                            ID: res.data.ID,
                            Name: res.data.Title
                        });
                        this.channelInput = '';
                        this.showChannelAddPopover = false;                       
                    }
                }
                catch{

                }
                finally{
                    this.loading= false;
                }
            }
            else if(foundIndex>0 && this.channelInput){
                this.channelNameAlreadyExist();
                this.channelInput = '';
                this.showChannelAddPopover = false;

            }
        },
        confirmRemove(){
            this.$modal.show('dialog',{
                text: `Are you sure to delete ${this.activeChannel.Name}?`,
                buttons:[
                    {title: 'Close'},
                    {
                        title: 'Delete',
                        handler: () => {
                            this.$modal.hide('dialog')
                            this.remove()
                        }
                    }
                ]
            })
        },
        async remove(){
            this.modalLoading = true;
            try{
                var res = await axios.delete(`channel?id=${this.activeChannel.ID}`);
                if(res.status == 200 && res.data.status == 'success'){
                    this.list = this.list.filter(x => x.ID != this.activeChannel.ID);
                    this.$modal.hide('channel');
                }
            }catch{
                this.modalLoading = false;
            }
        },
        async update(){  
            //console.log("editInput="+this.editInput+"\n Active name="+this.activeChannel.Name);   
            var foundIndex = this.list.findIndex(x=> x.Name == this.editInput); 
            //console.log("foundIndex="+foundIndex);                             
            if(this.editInput && this.editInput != this.activeChannel.Name && foundIndex<0){ 
                
                this.modalLoading = true;                         
                try{
                    var res = await axios.post("channel/upsert",
                        {ID:this.activeChannel.ID,
                        Title: this.editInput}
                    );
                    if(res.status == 200){
                        var foundIndex = this.list.findIndex(x => x.ID == this.activeChannel.ID);
                        this.list[foundIndex].Name = this.editInput;
                        this.editInput = '';
                        this.$modal.hide('channel');
                    }
                }
                catch{

                }finally{
                    this.modalLoading = false;
                }
            }
            else if(this.editInput && this.editInput != this.activeChannel.Name && foundIndex>0){
                this.channelNameAlreadyExistForEdit();
            }
            
        },

        
    },
    async mounted() {
            try{
                this.loadingOverlay=true;
                var res = await axios.get("channel/compactlist");
                if(res.status == 200){
                    this.list = res.data;
                    this.loadingOverlay=false;
                }
            }catch(e){
                auth.checkAPIResponse(e.response);
            }
        },
}
</script>
<style lang="scss" scoped>
    @import '~bootstrap/scss/bootstrap.scss';
    .more{
        cursor: pointer !important;
        padding: 10px !important;
    }
    .channel{
        padding: 10px;
        font-size: 1.2em;
        border: 1px solid rgba(0,0,0,.2);
        border-radius: .7em;
        color: $secondary;
        font-weight: 300;
        margin-top: 10px;
        i{
            font-size: 1.5em;
            color: $primary;
            cursor: pointer;
            border-radius: 1em;
            transition: all .2s;
            &:hover{
                background: $primary;
                color: $white;
            }
        }
    }
    .tippy-tooltip.bordered-theme{
        border: 1px solid #0062cc !important;
    }
</style>